import React from "react";
import * as actions from "./../Util/FetchData";
import DataLoading from "./../commons/DataLoading";
import DisplayList from "./../presentational/DisplayList";
import CreateListForm from "./../presentational/CreateListForm";
import AddSubForm from "./../presentational/AddSubForm";
import toaster from "toastr";

class Actions extends React.Component{
    constructor(props){
        super(props);
        this.createListAction = this.createListAction.bind(this);
        this.getListsAction = this.getListsAction.bind(this);
        this.addSubAction = this.addSubAction.bind(this);
        this.stopLoadingAnimation = this.stopLoadingAnimation.bind(this);
        this.onChange = this.onChange.bind(this);
        this.addSub = this.addSub.bind(this);
        this.createList = this.createList.bind(this);
        this.accessKey = this.props.params.id.replace(/-/g, "/");
        this.lists = null;

        this.state = {
            selectedOption: "",
            loading: false,
            listName : "",
            subEmail : "",
            ListId : ""
        };
    }
    //method to stop the busy animation
    stopLoadingAnimation(){
        let tempState = this.state;
        tempState.loading = false;
        this.setState(tempState);
    }

    //Get the lists belong to a client.
    getListsAction(){
        //set the selected action to list action and start loading animation
        let tempState = this.state;
        tempState.selectedOption = "getList";
        tempState.loading = true;
        this.setState(tempState);

        //get list through fetch async call
        let createListCall = actions.getLists(this.accessKey);
        //handle the promise
        createListCall.then((resolved) => {
            console.log("resolved");
            console.log(resolved);
            if(resolved.ok){
                resolved.json().then((content)=>{
                    console.log(content);
                    console.log("list updated");
                    this.lists = content;
                    this.stopLoadingAnimation();
                }).catch(()=>{
                    throw "can not get the content";
                });
            } else {toaster.error("Failed To Get List!!");}
        }).catch((reject) => {
            console.log(reject);
            this.stopLoadingAnimation();
            console.log("execution failed");
            toaster.error("Failed To Get List!!");
        });
    }

    //Create a list
    createListAction(event){
    event.preventDefault();
    //call async fetch to create list
        let createListCall = actions.createList(this.state.listName, this.accessKey);
        //handle promise
        createListCall.then((resolved) => {
            console.log(resolved);
            if(resolved.ok){
                resolved.json().then((content)=>{
                    toaster.success("List Created Successfully!!");
                    this.setState({listId: content});
                }).catch(()=>{
                    throw "can not get the content";
                });
            } else {toaster.error("Failed To Create List!!");}
        }).catch((reject) => {
            console.log(reject);
            console.log("execution failed");
            toaster.error("Failed To Create List!!");
        });
    }

    //This method adds a subscriber to the given list id
    addSubAction(event){
        event.preventDefault();
        //call fetch async to add subscriber
        let addSubCall = actions.addSubscriber(this.state.listId, this.state.subEmail, this.accessKey);
        //handle promise
        addSubCall.then((resolved) => {
            console.log(resolved);
            if(resolved.ok){
                console.log("addsub  resolved ok");
                resolved.json().then((content)=>{
                    toaster.success("Subscriber added successfully!!");
                }).catch(()=>{
                    throw "can not get the content";
                });
            } else {toaster.error("Failed To Add Subscriber!!");}
        }).catch((reject) => {
            console.log(reject);
            toaster.error("Failed To Add Subscriber!!");
        });

    }

    createList(){
        //set the selected action to create List action
        let tempState = this.state;
        tempState.selectedOption = "createList";
        this.setState(tempState);
    }

    //this reflects the modified text field
    onChange(event){
        let tempState = this.state;
        tempState[event.target.name] = event.target.value;
        this.setState(tempState);
    }

    addSub(){
        //set the selected action to add subscriber action
        let tempState = this.state;
        tempState.selectedOption = "addSub";
        this.setState(tempState);
    }


    render(){
        return (
            <div>
                <h3>Click on the actions below </h3>
                <button className="btn btn-primary actionbtn" onClick={this.getListsAction}>Get Lists</button>
                <button className="btn btn-primary actionbtn" onClick={this.createList}>Create List</button>
                <button className="btn btn-primary actionbtn" onClick={this.addSub}>Add Subscriber</button>
                {this.state.loading && <DataLoading/>}
                {!this.state.loading && this.state.selectedOption === "getList" && <DisplayList lists={this.lists}/>}
                {this.state.selectedOption === "createList" && <CreateListForm
                    listName={this.state.listName} onChange={this.onChange} onSubmit={this.createListAction}/>}
                {this.state.selectedOption === "addSub" && <AddSubForm
                    listId={this.state.listId} subEmail={this.state.subEmail}
                    onChange={this.onChange} onSubmit={this.addSubAction}/>}
            </div>
        );
    }

}

Actions.propTypes = {
};

export default Actions;


