export function getAccessToken(code, domainName) {


    let options = {
        method: 'POST',
        mode: 'cors'
    };

    const url = "https://api.createsend.com/oauth/token" +
        "?grant_type=authorization_code" +
        "&client_id=107741" +
        "&client_secret=6E3BvsOnC31f3GDZMhzg33tzK33BRE8dN3w7UR2383LQMtyNb331333cgO337HPf3q3mg3n71J3rOe3H" +
        "&code=" + code +
        "&redirect_uri="+ domainName + "/campmon";
    console.log("url: " + url);

     return fetch(url, options);

}

export function getLists(accessKey){
    console.log("Bearer " + accessKey);

    let options = {
        headers: {
            Authorization: "Bearer " + accessKey
        },
        method: "GET",
        mode: 'cors'
    };

    const url = "https://api.createsend.com/api/v3.1/clients/c249002d14a981c100799aa270c72536/lists.json";
    return fetch(url, options);

}

export function createList(listName, accessKey){
    console.log("Bearer " + accessKey);
    //get a random number to append to list name

    let options = {
        headers: {
            Authorization: "Bearer " + accessKey
        },
        method: "POST",
        mode: 'cors',
        body: JSON.stringify({
            "Title": listName,
            "UnsubscribePage": "",
            "UnsubscribeSetting": "",
            "ConfirmedOptIn": false,
            "ConfirmationSuccessPage": ""
        })
    };

    const url = "https://api.createsend.com/api/v3.1/lists/c249002d14a981c100799aa270c72536.json";
    console.log("create list url: " + url);

    return fetch(url, options);
}

export function addSubscriber(listId, subEmail, accessKey){
    console.log("Bearer " + accessKey);
    //get a random number to append to list name

    let options = {
        headers: {
            Authorization: "Bearer " + accessKey
        },
        method: "POST",
        mode: 'cors',
        body: JSON.stringify({
            "EmailAddress": subEmail,
            "Name": "New Subscriber",
            "Resubscribe": true,
            "RestartSubscriptionBasedAutoresponders": true,
            "CustomFields": [
                {
                    "Key": "website",
                    "Value": "http://example.com"
                },
                {
                    "Key": "interests",
                    "Value": "coding"
                }
            ]
        })
    };

    const url = "https://api.createsend.com/api/v3.1/subscribers/" + listId + ".json";
    console.log("create list url: " + url);

    return fetch(url, options);
}


