import React from "react";
import { PropTypes  } from "prop-types";

const DisplayList = ({lists}) => {

    return(
        <div>
            <h2>Display List</h2>
            <table className="table">
                <thead>
                <tr>
                    <th>List Id</th>
                    <th>Name</th>
                </tr>
                </thead>

                <tbody>
                {lists.map((list, index) => {
                    return (
                        <tr key={list.ListID}>
                            <td>{list.ListID}</td>
                            <td>{list.Name}</td>
                        </tr>);
                })}
                </tbody>
            </table>
        </div>
    );

};

DisplayList.propTypes = {
    lists: PropTypes.array
};


export default DisplayList;


