import React from "react";
import PropTypes from "prop-types";
import TextInput from "./../commons/TextInput";

const AddSubForm = ({listId, subEmail, onChange, onSubmit}) => {
    return(
        <div>
            <form>
                <TextInput
                    name="listId"
                    label="List ID"
                    value={listId}
                    onChange={onChange}
                    placeholder="Enter the repo name"
                    error=""
                />
                <TextInput
                    name="subEmail"
                    label="Subscriber Email"
                    value={subEmail}
                    onChange={onChange}
                    placeholder="Enter the repo name"
                    error=""
                />
                <input
                    type="submit"
                    disabled={false}
                    value="Submit"
                    className="btn btn-primary"
                    onClick={onSubmit}
                />
            </form>
        </div>
    );
};

AddSubForm.propTypes = {
    onSubmit : PropTypes.func,
    onChange : PropTypes.func,
    listId : PropTypes.string,
    subEmail : PropTypes.string
};

export default AddSubForm;


