import React from "react";
import {getAccessToken} from "./../Util/FetchData";
import { browserHistory } from "react-router";

class HomePage extends React.Component{
    constructor(props, context){
        super(props, context);
        this.onAuthenticate = this.onAuthenticate.bind(this);
        this.setOriginalRouting = this.setOriginalRouting.bind(this);
        //get the domain name to use it in redirect_url
        let fullpath = window.location.href;
        this.domainName = fullpath.substr(0,fullpath.indexOf("/", 8));
        this.getCode = this.getCode.bind(this);
        this.queryCode = this.props.location.query.code;
        if(this.queryCode === undefined){
            //This is original routing
            this.originalRouting = true;
        } else {
            //This is redirect after authentication
            this.originalRouting = false;
        }
        //variable to be used to attempt get the access token only once
        this.load = true;

        this.state = {
            authSuccess: true,
        };


    }

    onAuthenticate(){
        //send  authentication request
        let url = "https://api.createsend.com/oauth?" +
            "type=web_server" +
            "&client_id=107741" +
            "&redirect_uri=" + this.domainName + "/campmon" +
            "&scope=ViewReports%2CManageLists%2CImportSubscribers" +
            "&state=";
        console.log("url: " + url);
        window.location.replace(url);
    }

    getCode(){
        //get the access token
        let fetchCall = getAccessToken(this.queryCode, this.domainName);
        fetchCall.then((resolved) => {
            if(resolved.ok){
                resolved.json().then((content) => {
                    const key = content.access_token.replace(/\//g, "-");
                    browserHistory.push("campmon/Actions/" + key);
                }).catch((reject)=>{
                    console.log("resolved.json failed");
                    console.log(reject);
                    throw "Could not get the json response";
                });
            } else {throw "resolve not ok";}
        }).catch((rejected) => {
            console.log("failed to get the access token");
            this.setState({authSuccess: false});
        });
        this.load = false;
    }
    setOriginalRouting(){
        this.originalRouting = true;
    }


    render(){

        return(
            <div>
                {this.originalRouting && <h1>Access and Mange Campaign Monitor Account</h1>}
                {this.originalRouting && <h4>Please authenticate your account continue:</h4>}
                {this.originalRouting &&
                    <button className="btn btn-primary" onClick={this.onAuthenticate}>Authenticate</button>}
                {!this.originalRouting && this.load && this.getCode()}
                {!this.originalRouting && !this.state.authSuccess &&
                    <h3 className="errorRouting">Incorrect Routing Configuration. Please retry..</h3> }
                {!this.originalRouting && !this.state.authSuccess && this.setOriginalRouting()}
            </div>
        );
    }
}

export default HomePage;


